import React from "react";
import { ListGroup } from "react-bootstrap";

export default function SkillsList({ skills }) {
  return (
    <>
      <ListGroup className="mt-5">
        <ListGroup.Item>
          <b>{skills["header"]}</b>
        </ListGroup.Item>
        <ListGroup>
          {skills["skillsets"].map((skill) => {
            return <ListGroup.Item>{skill}</ListGroup.Item>;
          })}
        </ListGroup>
      </ListGroup>
    </>
  );
}
